// -#-#-#-#-#-#-#-#-#-#-#-#-#-#-#-#-#-#-
// -#- IMPORTS -#-
// -#-#-#-#-#-#-#-#-#-#-#-#-#-#-#-#-#-#-
// .
@import "_vars"; //       ,    .

// -#-#-#-#-#-#-#-#-#-#-#-#-#-#-#-#-#-#-
// -#- GLOBAL STYLES -#-
// -#-#-#-#-#-#-#-#-#-#-#-#-#-#-#-#-#-#-

html {
    box-sizing: border-box; //  , padding  border       ,         .
}

body {
    font-family: 'Montserrat', sans-serif; //  () .
    font-size: 15px; //  ()  .
    color: $lightBlack; //  ()  .
    line-height: 1.6; //  ()  .
    font-weight: 400; //  ()   (400 - regular).
    margin: 0;
}

*,
*::before,
*::after {
    box-sizing: inherit; //     ( html).
}

a {
    color: #000;
    text-decoration: none; //     .
    display: inline-block; //  ,     -.
}

ul,
li {
    list-style: none; //      .
    margin: 0; //  margin.
    padding: 0; //  padding.
}

h1, h2, h3, h4, h5, h6 {
    margin: 0;
}

hr { //   .
    display: block;
    width: 100%;
    height: 1px;
    border: 0;
    background-color: $superLightGray;
    margin: 40px 0;
}

input:focus,
button:focus {
    outline: none;
}

//     ,  header (  ,    .)
.page {
    overflow: hidden; //  ,    .
}

// container -    1200px +   15px    = 1230px.
.container {
    max-width: 1230px; //  ,      1230px,       .   1200px, a 1230px -      15px   .
    width: 100%;
    margin: 0 auto; //  .
    padding: 0 15px; //  ,                 max-width: 1200px.
}

// -#-#-#-#-#-#-#-#-#-#-#-#-#-#-#-#-#-#-
// -#- REUSABLE STYLES -#-
// -#-#-#-#-#-#-#-#-#-#-#-#-#-#-#-#-#-#-

//    : about, services, design, weDo, team, ourWork, clients, blog.
.global {
    &__header {
        max-width: 950px;
        width: 100%;
        margin: 0 auto 50px;
        text-align: center;
    }
    &__suptitle {
        font-size: 24px;
        font-family: 'Kaushan Script', cursive;
        color: $lightBlack;
    }
    &__title {
        font-size: 30px;
        font-weight: 700;
        color: $lightBlack;
        text-transform: uppercase;
        //   .
        &::after {
            content: "";
            width: 60px;
            height: 3px;
            display: block;
            background-color: $lightRed;
            margin: 30px auto 40px;
        }
    }
    &__text {
        font-size: 15px;
        font-family: 'Roboto', sans-serif;
        font-weight: 400;
        color: $lightGray;
        line-height: 24px;
    }
}

//   global__reviews-slider  : reviews1, reviews2.
.global {
    &__reviews {
        //  global__reviews-slider.
        &-slider {
            padding: 0 110px;
            position: relative;
        }
        //  html c Prev  Next    global__reviews-slider     main.js    slick: prevArrow  nextArrow.
        &-arrow {
            position: absolute; //   global__reviews-items.
            top: 50%;
            transform: translateY(-50%);
            z-index: 1;
            font-size: 0; //   Prev  Next,     html.
            color: transparent; //   Prev  Next,     html.
            //   item     ( ).
            &::after {
                content: "";
                display: block;
                width: 16px;
                height: 16px;
                border-top: 2px solid #ccc;
                border-left: 2px solid #ccc;
            }
            //   Prev.
            &--prev {
                left: 0;
                transform: rotate(-45deg);
            }
            //   Next.
            &--next {
                right: 0;
                transform: rotate(135deg);
            }
        }
        &-item {
            min-height: 210px;
            padding-left: 255px;
            position: relative;
        }
        &-photo {
            display: block;
            width: 210px;
            height: 210px;
            position: absolute; //  global__reviews-item.
            top: 50%;
            transform: translateY(-50%);
            left: 0;
            z-index: 1;
            border: 2px solid $lightGreen;
            border-radius: 50%;
        }
        &-text {
            font-family: 'Roboto', sans-serif;
            font-size: 24px;
            line-height: 36px;
            font-weight: 300;
            font-style: italic;
            color: $lightGray;
            margin-bottom: 15px;
        }
        &-author {
            font-family: 'Kaushan Script', cursive;
            font-size: 24px;
            color: #000;
            //   .
            &::before {
                content: "";
                width: 60px;
                height: 3px;
                display: inline-block;
                vertical-align: middle;
                background-color: $lightRed;
                margin-right: 10px;
            }
        }
    }
}



